.footer {
  background-color: #222;
  color: #fff;
  text-align: center;
  padding: 1rem 0;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  font-size: 1rem;
}

.footer a {
  color: #fff;
  text-decoration: underline;
  margin: 0 0.5rem;
}

.footer a:hover {
  color: #90caf9;
}