.header {
  width: 100%;
  box-sizing: border-box;
  background-color: #222;
  color: #fff;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.header-title {
  font-size: 1.8rem;
  font-weight: bold;
}

.header-nav a {
  color: #fff;
  margin-left: 2.5rem;
  text-decoration: none;
  font-size: 1rem;
}

.header-nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.header-nav a:hover {
  text-decoration: underline;
}